<template>
  <q-select
    :autofocus="autofocus"
    :dense="dense"
    outlined
    use-input
    multiple
    emit-value
    hide-bottom-space
    option-label="family"
    option-value="family"
    class="rounded text-body2 input-white text-primary"
    popup-content-class="bg-white rounded-bottom"
    input-class="text-body1 letter-space-normal text-primary"
    @filter="filterFn"
    :options="filteredOptions"
    v-model="selectModel"
    ref="selectFont"
    @input-value="(val) => (inputModel = val)"
    :rules="[(val) => (required ? !!val || '' : true)]"
  >
    <template v-slot:option="scope">
      <q-item
        clickable
        v-bind="scope.itemProps"
        :class="
          (scope.selected && !onlyEmitChanges) ||
          (onlyEmitChanges && initModel.includes(scope.opt.family))
            ? 'text-secondary bg-accent'
            : 'text-primary'
        "
        class="hover-text-secondary hover-bg-accent"
        @click="scope.opt?.action"
      >
        <q-item-section
          side
          v-if="onlyEmitChanges && initModel.includes(scope.opt.family)"
        >
          <q-icon
            size="xs"
            name="icon-check"
            :color="
              (scope.selected && !onlyEmitChanges) ||
              (onlyEmitChanges && initModel.includes(scope.opt.family))
                ? 'secondary'
                : 'primary'
            "
          />
        </q-item-section>
        <q-item-section>
          <q-item-label :class="{ [`gf-${scope.opt.family}`]: true }">
            {{ scope.opt?.family || scope.opt }}
          </q-item-label>
        </q-item-section>
      </q-item>
    </template>

    <template v-slot:selected>
      <span
        :class="{ 'text-placeholder': !selectModel?.family }"
        class="row items-center q-gutter-x-sm"
      >
        <q-icon size="xs" :name="formatPlatformIconSrc('Google Font')" />
        <span>{{
          inputModel
            ? ''
            : onlyEmitChanges
            ? initializing
              ? 'Loading google fonts...'
              : placeholder
            : selectModel.family || placeholder
        }}</span>
      </span>
    </template>
  </q-select>
</template>

<script>
import { defineComponent, ref } from 'vue';
import { mapActions } from 'pinia';
import useHelpers from 'stores/helpers';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'SelectFont',

  emits: ['updated'],

  props: {
    autofocus: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: 'Select from Google Fonts',
    },
    initModel: {
      type: Object || Array,
      default: () => {},
    },
    onlyEmitChanges: {
      type: Boolean,
      default: true,
    },
    dense: {
      type: Boolean,
      default: true,
    },
    required: {
      type: Boolean,
      default: false,
    },
  },

  watch: {
    initModel: {
      async handler(val) {
        if (val) this.selectModel = val;
        this.createStyle();
      },
      deep: true,
    },
    selectModel: {
      async handler(val) {
        if (val) this.$emit('updated', val);
      },
    },
  },

  data() {
    return {
      selectModel: [],
      inputModel: '',
      initializing: true,
      fontList: [],
    };
  },

  setup() {
    return {
      filteredOptions: ref([]),
    };
  },

  async mounted() {
    if (this.initModel) this.selectModel = this.initModel;
    const response = await this.getGoogleFonts('popularity', true);
    if (response.success) this.fontList = response.data?.items;
    this.createStyle();
    this.initializing = false;
  },

  computed: {
    allFonts() {
      return this.fontList.map((e) => e.family);
    },
  },

  methods: {
    ...mapActions(useHelpers, ['getGoogleFonts']),
    ...mapActions(useGrowmodo, ['formatPlatformIconSrc']),

    async createStyle() {
      const style = document.createElement('style');
      style.classList.add('GoogleFonts-Style');
      style.innerHTML = '';
      // const modelFamily = this.selectModel;
      for (const font of this.fontList) {
        // if (modelFamily.includes(font.family)) {
        style.innerHTML += `
          @font-face {
            font-family: '${font.family}';
            src: url('${font.files.regular || Object.values(font.files)[0]}');
          }
          .gf-${font.family?.replaceAll?.(' ', '_')} {
            font-family: '${font.family}'
          }`;
        // }
      }
      this.$refs['selectFont'].$el.appendChild(style);
    },

    filterFn(val, update, abort) {
      if (val === '') {
        update(() => {
          this.filteredOptions = this.fontList;
        });
        return;
      }
      update(() => {
        const needle = val.toLowerCase();
        this.filteredOptions = this.fontList.filter(
          (v) => JSON.stringify(v).toLowerCase().indexOf(needle) > -1
        );
      });
    },
  },
});
</script>
