<template>
  <q-card flat class="q-pa-sm rounded" style="width: 360px">
    <q-card-section>
      <q-toolbar class="bg-transparent q-pa-none">
        <CardIconBox icon="icon-file-check-03" />

        <div v-if="!noclose" class="absolute-top-right">
          <close-button />
        </div>
      </q-toolbar>
    </q-card-section>
    <q-card-section class="q-py-none">
      <div class="text-h5 font-demi-bold letter-space-normal q-pb-sm">
        Rename File
      </div>
      <div v-if="0" class="text-body2 q-py-md">
        <span v-for="(p, i) in file_path" :key="`${p}-${i}`">
          <span class="text-secondary">/</span>
          {{ p }}
          <span class="text-secondary" v-if="file_path.length - 1 === i"
            >/</span
          >
        </span>
      </div>
      <div class="q-py-sm">
        <InputTextField
          v-model="newFileName"
          :placeholder="fileName"
          class="q-pb-none"
          :rules="[(val) => filename_pattern.test(val) || '']"
          @keyup.enter="submitChanges"
          @keyup.delete="onDelete"
        >
          <template v-slot:append>
            <span class="text-body2"> {{ fileExt }} </span>
          </template>
        </InputTextField>
      </div>
    </q-card-section>
    <q-card-actions align="right" class="full-width q-gutter-sm q-py-md">
      <q-btn
        no-caps
        outline
        unelevated
        class="col rounded button-gm-outline-dark"
        :disable="submitting"
        @click="$emit('close')"
        v-close-popup
      >
        <span class="text-body1 font-medium">Cancel</span>
      </q-btn>
      <q-btn
        no-caps
        unelevated
        color="primary"
        class="col rounded"
        :disable="submitting || !renameReady"
        @click="submitChanges"
      >
        <span class="text-body1 font-medium">Rename</span>
      </q-btn>
    </q-card-actions>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import { mapActions } from 'pinia';
import useHelpers from 'stores/helpers';
import CardIconBox from 'src/components/Helpers/CardIconBox.vue';
import InputTextField from 'src/components/Helpers/InputTextField.vue';
import { extractErrorMessageString } from 'src/assets/scripts/functions.js';

export default defineComponent({
  name: 'RenameFile',

  emits: ['close', 'success', 'failed'],

  components: { CardIconBox, InputTextField },

  props: {
    noclose: {
      type: Boolean,
      default: false,
    },
    file: {
      type: Object,
      default: () => {},
    },
  },

  data() {
    return {
      newFileName: '',
      submitting: false,
      submitted: false,
      filename_pattern: /^([a-zA-Z0-9 \._-]+){1}$/,
      file_path: [],
    };
  },

  computed: {
    fileName() {
      return this.file?.file?.split?.(this.fileExt)?.[0] || '';
    },
    fileExt() {
      const fileExtra = this.file?.file?.split?.('.');
      return fileExtra ? `.${fileExtra[fileExtra.length - 1]}` : '';
    },
    renameReady() {
      return this.newFileName
        ? this.filename_pattern.test(this.newFileName)
        : false;
    },
  },

  watch: {
    newFileName: {
      async handler(val) {
        if (0 && val && val.includes('/')) {
          let n = val.split('/');
          if (n[0]) {
            this.file_path.push(n[0]);
            this.newFileName = n[1];
          }
        }
      },
    },
  },

  mounted() {
    if (this.fileName) this.newFileName = this.fileName;
    if (!this.file?.id) this.$emit('close');
    const split_path = this.file.path.split('/');
    this.file_path = split_path.slice(3, split_path.length - 1);
  },

  methods: {
    ...mapActions(useHelpers, ['renameFile']),

    onDelete() {
      if (!this.newFileName) {
        this.newFileName = this.file_path.pop();
      }
    },

    async submitChanges() {
      if (!this.filename_pattern.test(this.newFileName)) return;
      this.submitting = true;
      if (!this.newFileName) return;
      if (this.fileName === this.newFileName) {
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: 'No changes has been made',
        });
        this.$emit('close');
      } else {
        const response = await this.renameFile(this.file?.id, this.newFileName);
        const message = extractErrorMessageString(response?.message);
        if (response.success) {
          this.$q.notify({
            icon: 'icon-check-circle-broken',
            message: message || 'File has been renamed',
          });
          this.submitted = true;
          this.$emit('success', response.data);
          this.$emit('close');
        } else {
          this.$q.notify({
            icon: 'icon-alert-triangle',
            iconColor: 'negative',
            message: message || 'Failed to rename file',
          });
          this.$emit('failed', message);
        }
      }
      this.submitting = false;
    },
  },
});
</script>
