function getPlatformInfo(platform, key = null) {
  return platform
    ? this.platforms[platform] && key
      ? this.platforms?.[platform]?.[key]
      : this.platforms?.[platform]
    : 0;
}

function formatPlatformIconSrc(platform) {
  const info = this.getPlatformInfo(platform) || {};
  return (
    'img:' +
    (info.icon && info.icon.startsWith('https://')
      ? `${info.icon}`
      : `${cdn.baseURL}${cdn.platforms}/${info.title || platform}.png`)
  );
}

function planPriceCalculator(talentCnt, billingId) {
  const helpers = useHelpers();
  if (!talentCnt || !billingId) return {};
  const { formatMoney } = require('src/assets/scripts/functions');
  const billing = this.subscription_BillingTypes[billingId] || {};

  // Total Price
  let total =
    talentCnt === -1 || talentCnt === 6
      ? 0
      : this.subscription.base_price * talentCnt * billing.value;
  // Discount
  let discount =
    (billing.savings_percentage ? total * billing.savings : billing.savings) ||
    0;
  // Discount Rounded by 50
  let discount_rounded = Math.round(discount / 50) * 50;
  // Total Discount check if savings_per_month
  let discount_total =
    discount_rounded * (billing.savings_per_month ? billing.value : 1);
  // Total Discounte Price
  let total_discounted = total - discount_total;
  // Discount Label
  let discount_label =
    total === 0
      ? 'Custom'
      : helpers.getCurrency(billing.savings_currency)?.symbol +
        formatMoney(discount_total);
  // Final Label with Total Discounted Price
  let label =
    total === 0
      ? 'Custom'
      : helpers.getCurrency(this.subscription.savings_currency)?.symbol +
        formatMoney(total_discounted);

  return {
    label,
    total,
    total_discounted,
    discount,
    discount_rounded,
    discount_label,
    discount_total,
  };
}

