/* Request Methods */
// Task Request
async function requestTask(payload, orgId) {
  if (!orgId) {
    const user = useUser();
    orgId = user.activeOrgID;
    if (!orgId) return;
  }
  const url = `/organizations/${orgId}/tasks`;
  return await api
    .post(url, payload)
    .then((res) => {
      const data = res.data || {};
      return data;
    })
    .catch((e) => {
      return e;
    });
}
