<template>
  <q-input
    :autofocus="autofocus"
    :dense="dense"
    outlined
    autocomplete
    hide-bottom-space
    :type="inputType"
    v-model="passwordModel"
    :label="label"
    :placeholder="
      label
        ? ''
        : placeholder
        ? placeholder
        : inputType === 'password'
        ? '********'
        : '--------'
    "
  >
    <template v-slot:prepend v-if="withicon">
      <q-icon
        :size="dense ? '20px' : 'sm'"
        name="icon-lock-01"
        class="hover-text-secondary"
      />
    </template>
    <template v-slot:append>
      <q-btn
        v-if="passwordModel"
        flat
        :dense="dense"
        round
        size="md"
        color="primary-300"
        :icon="inputType == 'password' ? 'icon-eye' : 'icon-eye-off'"
        class="bg-none"
        :ripple="false"
        @click="tooglePassword"
      />
    </template>
  </q-input>
</template>

<script>
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'InputPassword',

  props: {
    autofocus: {
      type: Boolean,
      default: false,
    },
    label: {
      type: String,
    },
    placeholder: {
      type: String,
      default: null,
    },
    withicon: {
      type: Boolean,
      default: false,
    },
    dense: {
      type: Boolean,
      default: true,
    },
  },

  data() {
    return {
      passwordModel: '',
      inputType: 'password',
    };
  },

  methods: {
    tooglePassword(forceValue = null) {
      if (this.inputType === undefined) return;
      if (forceValue !== null && typeof forceValue === 'boolean')
        this.inputType = forceValue;
      else {
        this.inputType =
          this.inputType?.toLowerCase() === 'password' ? 'text' : 'password';
      }
    },
  },
});
</script>
