<template>
  <q-card class="rounded q-pa-md">
    <div
      v-if="colorPaletteSrc && colorViewPalette"
      class="text-body2 font-medium q-pb-sm"
    >
      Colors from the image
    </div>
    <q-color
      no-header
      no-footer
      ref="customColorPicker"
      :palette="colorPaletteSrc"
      :default-view="
        colorPaletteSrc && colorViewPalette ? 'palette' : 'spectrum'
      "
      v-model="colorModel"
      :format-model="colorFormat?.value"
    />
    <div
      v-if="colorPaletteSrc"
      :class="colorPaletteSrc && colorViewPalette ? 'q-py-md' : 'q-pb-md'"
    >
      <div class="row q-gutter-xs items-center justify-center">
        <q-card
          v-for="(color, i) in colorPaletteSrc.slice(
            0,
            colorPaletteSrc.length > 30 ? 30 : colorPaletteSrc.length
          )"
          :key="i"
          flat
          class="col-auto q-pa-md cursor-pointer rounded"
          :style="`background-color: ${color};`"
          @click="
            colorModel =
              colorFormat.value === 'rgba' ? color : rgbToHex(textToRgb(color))
          "
        >
          <tooltip-pro>{{
            colorFormat.value === 'rgba' ? color : rgbToHex(textToRgb(color))
          }}</tooltip-pro>
        </q-card>
      </div>
    </div>
    <div
      class="row q-col-gutter-sm"
      :class="colorPaletteSrc && colorViewPalette ? 'q-py-md' : 'q-pb-md'"
    >
      <div
        class="col-7"
        @mouseover="showCopy = true"
        @mouseout="showCopy = false"
        @mouseleave="showCopy = false"
      >
        <q-input
          dense
          outlined
          v-model="colorModel"
          class="cursor-pointer"
          input-class="cursor-pointer"
          @dblclick="copyToClipboard(colorModel)"
        >
          <template v-slot:append v-if="colorModel && showCopy">
            <q-icon
              name="icon-copy-06"
              size="xs"
              color="secondary"
              class="cursor-pointer"
              @click="copyToClipboard(colorModel)"
            />
          </template>
        </q-input>
      </div>
      <div class="col-5">
        <q-select
          dense
          outlined
          v-model="colorFormat"
          class="rounded text-body2 input-white text-primary"
          popup-content-class="bg-white rounded-bottom"
          input-class="text-body1 letter-space-normal text-primary"
          :options="Object.values(colorFormats)"
        />
      </div>
    </div>
    <div class="q-gutter-sm row">
      <q-btn
        no-caps
        outline
        unelevated
        color="primary"
        class="col"
        @click="cancelColor"
      >
        Discard
      </q-btn>
      <q-btn no-caps unelevated color="primary" class="col" @click="saveColor">
        Save Color
      </q-btn>
    </div>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import { colors, copyToClipboard } from 'quasar';
const { rgbToHex, textToRgb } = colors;

export default defineComponent({
  name: 'ColorPicker',

  emits: ['updated', 'saved', 'canceled', 'closed'],

  props: {
    colorPalette: {
      type: Array,
    },
    initColor: {
      type: String,
      default: '',
    },
    colorViewPalette: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    const colorFormats = {
      hexa: { label: 'HEX', value: 'hexa' },
      rgba: { label: 'RGB', value: 'rgba' },
    };
    return {
      rgbToHex,
      textToRgb,
      colorModel: '',
      colorFormats,
      showCopy: false,
      colorPaletteSrc: undefined,
      customColorPicker: null,
      colorFormat: colorFormats.hexa,
    };
  },

  computed: {
    responseVal() {
      return {
        format: this.colorFormat?.value,
        color: this.colorModel,
      };
    },
  },

  watch: {
    ['colorFormat.value']: {
      async handler(val) {
        if (val === 'rgba') {
          let l_rgba = textToRgb(this.colorModel);
          this.colorModel = `rgba(${l_rgba.r},${l_rgba.g},${l_rgba.b},${
            l_rgba.a / 100 || 1
          })`;
        } else {
          this.colorModel = rgbToHex(textToRgb(this.colorModel));
        }
      },
    },
    colorModel: {
      async handler(val) {
        let parentElemToStyle = this.$refs?.['customColorPicker']?.$el;
        if (parentElemToStyle) {
          let ringIcons = parentElemToStyle.querySelectorAll(
            '.q-slider__focus-ring'
          );
          // Set Rings Color
          ringIcons.forEach((c, i) => {
            c?.classList?.add?.('icon-circle-01', 'q-icon', 'border-add');
            c?.style?.setProperty?.(
              '--ringColor',
              i === 0 ? this.alwaysToHex(val) : val
            );
          });
          // Set Alpha Track Bar color
          let trackBar = parentElemToStyle.querySelector(
            '.q-color-picker__alpha .q-slider__track'
          )?.style;
          trackBar?.setProperty?.(
            '--barColor',
            `linear-gradient(90deg, rgba(255, 255, 255, 0), ${this.alwaysToHex(
              val
            )})`
          );
        }

        this.$emit('updated', { format: this.colorFormat?.value, color: val });
      },
    },
    colorPalette: {
      async handler(val) {
        this.colorPaletteSrc = val;
      },
      deep: true,
    },
  },

  mounted() {
    this.colorModel = this.initColor || '#000000ff';
    this.colorPaletteSrc = this.colorPalette;
  },

  methods: {
    copyToClipboard(stringVal) {
      copyToClipboard(stringVal).then(() => {
        if (stringVal) {
          this.$q.notify({
            icon: 'icon-check-circle-broken',
            message: 'Copied to clipboard.',
          });
        }
      });
    },

    saveColor() {
      this.$emit('saved', this.responseVal);
      this.closeMethod();
    },

    cancelColor() {
      this.$emit('canceled', this.responseVal);
      this.closeMethod();
    },

    closeMethod() {
      this.$emit('closed', this.responseVal);
    },

    alwaysToHex(colorString) {
      try {
        if (colorString.charAt(0) === '#') return colorString.substring(0, 7);
        return rgbToHex(textToRgb(colorString)).substring(0, 7);
      } catch (e) {
        return colorString;
      }
    },
  },

  beforeUnmount() {
    this.closeMethod();
  },
});
</script>

