  border-radius: $border;
  // &__sliders {
  //   padding: 0;
  // }
  & .q-panel-parent {
    border-radius: $border;
  }
  &__spectrum {
    border-radius: $border;
    &-white,
    &-black {
      border-radius: $border;
    }
  }
  &__palette {
    &-tab,
    &-rows {
      border-radius: $border;
    }
  }
  & .q-slider {
    &__focus-ring {
      &:hover,
      &:active,
      &:focus-within,
      &:focus-visible {
        transform: scale3d(1, 1, 1) !important;
      }
      text-align: center;
      opacity: 1 !important;
      background: white !important;
      &:before {
        opacity: 1 !important;
        border-radius: 50%;
        background: white !important;
        color: var(--ringColor) !important;
      }
    }
    &__track {
      height: 20px !important;
      &:before {
        background: var(--barColor) !important;
      }
      &-container {
        &:hover,
        &:active,
        &:focus-within,
        &:focus-visible {
          & .q-slider__focus-ring {
            transform: scale3d(1, 1, 1) !important;
          }
        }
      }
    }
    &__thumb {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      & svg {
        display: none;
      }
    }
  }
}
</style>
