<template>
  <q-select
    :autofocus="autofocus"
    :dense="dense"
    outlined
    use-input
    emit-value
    hide-bottom-space
    option-value="name"
    class="rounded text-body2 input-white text-primary"
    popup-content-class="bg-white rounded-bottom"
    input-class="text-body1 letter-space-normal text-primary"
    @filter="filterFn"
    :options="filteredOptions"
    v-model="selectModel"
    @input-value="(val) => (inputModel = val)"
    :rules="[(val) => (required ? !!val || '' : true)]"
  >
    <template v-slot:option="scope">
      <q-item
        clickable
        v-bind="scope.itemProps"
        :class="scope.selected ? 'bg-accent text-secondary' : 'text-primary'"
        class="hover-text-secondary hover-bg-accent"
        @click="scope.opt?.action"
      >
        <q-item-section avatar v-if="scope.opt?.code" style="min-width: unset">
          <q-icon
            size="xs"
            :name="`img:${getCountryFlag(scope.opt.code?.toLowerCase?.())}.svg`"
          />
        </q-item-section>
        <q-item-section>
          <q-item-label>
            {{ scope.opt?.name || scope.opt }}
          </q-item-label>
        </q-item-section>
      </q-item>
    </template>

    <template v-slot:selected>
      <span
        :class="{ 'text-placeholder': !selectModel }"
        class="row items-center q-gutter-x-sm"
      >
        <q-icon
          v-if="!inputModel && selectModel"
          size="xs"
          :name="`img:${getCountryFlag(
            currentCountryInfo.code?.toLowerCase?.()
          )}.svg`"
        />
        <span>{{ inputModel ? '' : selectModel || placeholder }}</span>
      </span>
    </template>
  </q-select>
</template>

<script>
import { defineComponent, ref } from 'vue';
import { mapActions, mapState } from 'pinia';
import useHelpers from 'stores/helpers';

export default defineComponent({
  name: 'SelectCountry',

  props: {
    autofocus: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: 'Select country',
    },
    initModel: {
      type: [String, Number],
      default: '',
    },
    dense: {
      type: Boolean,
      default: true,
    },
    required: {
      type: Boolean,
      default: false,
    },
  },

  watch: {
    initModel: {
      async handler(val) {
        if (val) this.selectModel = val;
      },
    },
  },

  data() {
    return {
      selectModel: '',
      inputModel: '',
    };
  },

  setup() {
    return {
      filteredOptions: ref([]),
    };
  },

  mounted() {
    if (this.initModel) this.selectModel = this.initModel;
  },

  computed: {
    ...mapState(useHelpers, ['allCountries']),

    currentCountryInfo() {
      let res = this.allCountries.filter((c) => c.name === this.selectModel)[0];
      if (res) return res;
      res = this.allCountries.filter((c) =>
        c.name.startsWith(this.selectModel)
      )[0];
      this.changeModel(res.name);
      return res || {};
    },
  },

  methods: {
    ...mapActions(useHelpers, ['getCountryFlag']),

    changeModel(modelVal) {
      this.selectModel = modelVal;
    },

    filterFn(val, update, abort) {
      if (val === '') {
        update(() => {
          this.filteredOptions = this.allCountries;
        });
        return;
      }
      update(() => {
        const needle = val.toLowerCase();
        this.filteredOptions = this.allCountries.filter(
          (v) => JSON.stringify(v).toLowerCase().indexOf(needle) > -1
        );
      });
    },
  },
});
</script>
