<template>
  <q-input
    :autofocus="autofocus"
    outlined
    hide-bottom-space
    :dense="dense"
    v-model="internalModel"
    placeholder="www.yourwebsite.com"
    :rules="[
      (val) =>
        checker.input(
          {
            value: val ? `https://${val}` : val,
            nomsg: true,
            type: 'url',
          },
          500,
          !required
        ),
    ]"
    :class="{ ['input-split']: split }"
  >
    <template v-slot:prepend>
      <div class="text-body2 text-primary-500">{{ prepend }}</div>
      <q-separator vertical class="q-ml-sm" />
    </template>
  </q-input>
</template>

<script>
import { defineComponent } from 'vue';
import { checker } from 'src/assets/scripts/functions';

export default defineComponent({
  name: 'InputWebsite',

  props: {
    autofocus: {
      type: Boolean,
      default: false,
    },
    split: {
      type: Boolean,
      default: true,
    },
    prepend: {
      type: String,
      default: 'https://',
    },
    required: {
      type: Boolean,
      default: false,
    },
    dense: {
      type: Boolean,
      default: true,
    },
  },

  data() {
    return {
      checker,
      internalModel: '',
    };
  },
});
</script>
