<template>
  <q-card flat bordered>
    <q-card-section
      class="row q-col-gutter-md items-center justify-between card-padding-title"
    >
      <!-- Top Left -->
      <div class="col-12 col-md-6">
        <q-btn-group
          :spread="$q.screen.sm || $q.screen.lt.md"
          class="rounded no-shadow border-add"
        >
          <q-btn
            flat
            no-caps
            class="hover-text-secondary"
            :color="tableTab === 'tasks' ? 'secondary' : 'primary-600'"
            :class="{ 'bg-accent': tableTab === 'tasks' }"
            @click="tableTab = 'tasks'"
          >
            <span class="text-body1 font-medium">All Tasks</span>
          </q-btn>
          <q-separator vertical />
          <q-btn
            flat
            no-caps
            class="hover-text-secondary"
            :color="tableTab === 'projects' ? 'secondary' : 'primary-600'"
            :class="{ 'bg-accent': tableTab === 'projects' }"
            @click="tableTab = 'projects'"
          >
            <span class="text-body1 font-medium">All Projects</span>
          </q-btn>
        </q-btn-group>
      </div>

      <!-- Top Right -->
      <div class="col-12 col-md-6">
        <div class="row q-col-gutter-md items-center justify-end text-right">
          <div class="col-12 col-md-9">
            <q-input
              v-model="searchQuery"
              dense
              outlined
              clearable
              debounce="500"
              input-class="text-body1 font-medium text-primary"
              placeholder="Search..."
              clear-icon="icon-x-close"
            >
              <template v-slot:prepend>
                <q-icon name="icon-search-lg" class="hover-text-secondary" />
              </template>
            </q-input>
          </div>

          <div v-if="false">
            <q-select
              dense
              multiple
              outlined
              emit-value
              map-options
              color="primary"
              hide-dropdown-icon
              :options="columns"
              :options-dense="false"
              :options-cover="true"
              v-model="visibleColumns"
              display-value="Filters"
              :option-value="(val, row) => (val ? val.name : null)"
              class="hover-text-secondary table-select-filter"
              transition-show="jump-up"
              transition-hide="jump-down"
            >
              <template v-slot:prepend>
                <q-icon
                  name="icon-filter-lines"
                  size="sm"
                  color="primary"
                  class="hover-text-secondary"
                />
              </template>
              <template v-slot:option="scope">
                <q-item
                  v-bind="scope.itemProps"
                  :disable="scope.opt.required"
                  class="hover-text-secondary"
                  :class="{ ['hover-bg-accent']: !scope.opt.required }"
                >
                  <q-item-section>
                    <q-item-label
                      :class="
                        scope.opt.required || scope.selected
                          ? 'text-secondary'
                          : ''
                      "
                    >
                      {{ scope.opt.label }}
                    </q-item-label>
                  </q-item-section>
                </q-item>
              </template>
            </q-select>
          </div>
        </div>
      </div>
    </q-card-section>

    <q-table
      flat
      bordered
      row-key="id"
      filteredSortedRows
      binary-state-sort
      :rows="orgTmpItems"
      :columns="columns"
      color="primary"
      :loading="tableLoading"
      :dense="listTableDense"
      rows-per-page-label="Users per page"
      no-results-label="The filter didn't uncover any results."
      loading-label="Getting data from the server."
      :rows-per-page-options="[10, 20, 30, 50, 50, 0]"
      style="max-height: 950px"
      selection="multiple"
      v-model:pagination="pagination"
      v-model:selected="selectedFiles"
      :visible-columns="visibleColumns"
      @request="onRequest"
    >
      <!-- Loading -->
      <template v-slot:loading>
        <div class="bg-color absolute-top table-loader" style="top: 43px">
          <q-linear-progress size="2px" indeterminate color="secondary" />
        </div>
      </template>

      <!-- Table Header Slot -->
      <template v-slot:header="scope">
        <q-tr class="bg-color" style="position: sticky; top: 0; z-index: 2">
          <q-th
            auto-width
            class="cursor-pointer hover-text-secondary text-left"
            @click.stop="scope.selected = !scope.selected"
          >
            <q-icon
              flat
              round
              size="sm"
              :name="scope.selected ? 'icon-check-square' : 'icon-square'"
              :color="
                scope.selected || selectedFiles.length
                  ? 'secondary'
                  : 'primary-300'
              "
            />
          </q-th>
          <q-th
            v-for="(col, i) in scope.cols"
            :key="col.name"
            :props="scope"
            :auto-width="i === 1 && i !== scope.cols.length - 1"
            :style="i === 0 ? 'padding-left: 0' : ''"
          >
            <span class="text-body2 font-medium text-primary-500">
              {{ col.label }}
            </span>
          </q-th>
          <!-- Files Options -->
          <q-th auto-width class="text-right">
            <span class="text-body2 font-medium text-transparent">Options</span>
          </q-th>
        </q-tr>
      </template>

      <!-- Table Body Slot -->
      <template v-slot:body="scope">
        <q-tr
          :props="scope"
          class="hover-bg-accent"
          :class="{
            [scope.selected ? 'bg-accent' : 'bg-transparent']: true,
            ['no-pointer-events']: tableLoading,
          }"
        >
          <template v-if="tableLoading">
            <q-td
              class="bg-transparent cursor-pointer text-primary hover-text-secondary"
            >
              <div class="row justify-center items-center text-center">
                <q-skeleton type="rect" size="25px" />
              </div>
            </q-td>
            <q-td
              v-for="(_r, i) in scope.cols"
              :key="_r.name || i"
              :props="scope"
              :class="scope.selected ? 'bg-accent' : 'bg-transparent'"
              class="cursor-pointer text-primary"
              :style="i === 0 ? 'padding-left: 0' : ''"
            >
              <!-- Item name with icon -->
              <q-item v-if="i === 0" class="text-body2 q-pa-none">
                <q-item-section avatar style="min-width: unset" class="q-pr-sm">
                  <CardIconBox
                    useSpinner
                    style="padding: 14px; max-width: 48px; max-height: 48px"
                  />
                </q-item-section>
                <q-item-section
                  class="cursor-pointer"
                  :class="{ 'font-medium': i === 0 }"
                >
                  <q-skeleton
                    type="text"
                    :width="`${150 + 10 * i}px`"
                    height="30px"
                  />
                </q-item-section>
              </q-item>
              <q-skeleton
                v-else-if="_r.name === 'task_priority'"
                type="text"
                width="50px"
                height="30px"
              />
              <!-- Item Info -->
              <span
                v-else
                class="text-body2 text-primary-500"
                :class="{ 'font-medium': i === 0 }"
              >
                <q-skeleton type="text" width="90%" height="30px" />
              </span>
            </q-td>
            <q-td
              class="bg-transparent"
              style="padding-top: 18px; padding-bottom: 18px"
            >
              <div class="text-right">
                <q-skeleton type="rect" size="25px" />
              </div>
            </q-td>
          </template>
          <template v-else>
            <q-td
              :class="scope.selected ? 'bg-accent' : 'bg-transparent'"
              class="cursor-pointer text-primary text-left hover-text-secondary"
              @click.stop="scope.selected = !scope.selected"
            >
              <q-icon
                flat
                round
                size="sm"
                :name="scope.selected ? 'icon-check-square' : 'icon-square'"
                :color="scope.selected ? 'secondary' : 'primary-300'"
              />
            </q-td>
            <q-td
              v-for="(_r, i) in scope.cols"
              :key="_r.name || i"
              :props="scope"
              :class="scope.selected ? 'bg-accent' : 'bg-transparent'"
              class="cursor-pointer text-primary"
              :style="i === 0 ? 'padding-left: 0' : ''"
            >
              <!-- Item name with icon -->
              <q-item v-if="i === 0" class="text-body2 q-pa-none">
                <q-item-section avatar style="min-width: unset" class="q-pr-sm">
                  <CardIconBox
                    :icon="
                      (tableTab === 'tasks'
                        ? taskDirectory[scope.row?.task_id]?.icon
                        : projectDirectory[scope.row?.project_id]?.icon) ||
                      'icon-browser'
                    "
                  />
                </q-item-section>
                <q-item-section
                  class="cursor-pointer"
                  :class="{ 'font-medium': i === 0 }"
                  @click="
                    $router.push(
                      `/brands/${brand_id || scope.row.brand_id}/${
                        tableTab === 'tasks' ? 'task' : 'project'
                      }/${scope.row?.id}`
                    )
                  "
                >
                  <span>
                    {{ _r.value }}
                  </span>
                </q-item-section>
              </q-item>
              <q-badge
                v-else-if="_r.name === 'task_priority'"
                :class="`badge-${
                  _r.value == 'urgent'
                    ? 'negative'
                    : _r.value == 'low'
                    ? 'blue'
                    : _r.value == 'medium'
                    ? 'warning'
                    : 'grey'
                }`"
                class="badge"
              >
                {{ capitalize(_r?.value || '') }}
              </q-badge>
              <!-- Item Info -->
              <span
                v-else
                class="text-body2 text-primary-500"
                :class="{ 'font-medium': i === 0 }"
              >
                {{ _r.value }}
              </span>
            </q-td>
            <q-td
              :class="scope.selected ? 'bg-accent' : 'bg-transparent'"
              style="padding-top: 18px; padding-bottom: 18px"
            >
              <div class="q-gutter-sm text-right">
                <q-btn
                  flat
                  round
                  size="sm"
                  color="primary-500"
                  :class="{ ['hover-text-negative']: !tableLoading }"
                  :disable="tableLoading"
                  @click.prevent
                >
                  <q-icon name="icon-trash-01" size="20px" />
                </q-btn>
              </div>
            </q-td>
          </template>
        </q-tr>
      </template>

      <!-- Bottom -->
      <template v-slot:bottom="scope">
        <div
          v-if="$q.screen.lt.xs || $q.screen.xs"
          class="q-py-md row full-width items-center justify-center q-col-gutter-sm q-virtual-scroll--skip"
        >
          <!-- Pagination -->
          <q-pagination
            flat
            unelevated
            boundary-numbers
            v-model="pagination.page"
            :disable="tableLoading"
            color="transparent"
            text-color="primary-500"
            active-color="primary-50"
            active-text-color="primary-700"
            :max="scope.pagesNumber"
            :max-pages="6"
          />
        </div>
        <div
          class="q-py-md row full-width items-center justify-between q-col-gutter-sm q-virtual-scroll--skip"
        >
          <!-- Previous Page -->
          <div class="text-left" :class="$q.screen.gt.xs ? 'col-2' : 'col-6'">
            <q-btn
              dense
              outline
              no-caps
              :disable="tableLoading || scope.isFirstPage"
              @click="scope.prevPage"
              class="rounded q-px-md slide-hover button-gm-outline-dark text-body1 font-medium button-sm"
              :class="{
                [scope.isFirstPage ? 'text-primary-500' : 'text-primary']: true,
                ['hover-text-secondary']: !scope.isFirstPage,
              }"
            >
              <q-icon
                size="20px"
                name="icon-arrow-narrow-left"
                class="q-pr-xs"
              />
              Previous
            </q-btn>
          </div>

          <!-- Pagination -->
          <q-pagination
            v-if="$q.screen.gt.xs"
            flat
            unelevated
            boundary-numbers
            v-model="pagination.page"
            :disable="tableLoading"
            color="transparent"
            text-color="primary-500"
            active-color="primary-50"
            active-text-color="primary-700"
            :max="scope.pagesNumber"
            :max-pages="6"
          />

          <!-- Next Page -->
          <div class="text-right" :class="$q.screen.gt.xs ? 'col-2' : 'col-6'">
            <q-btn
              dense
              outline
              no-caps
              :disable="tableLoading || scope.isLastPage"
              @click="scope.nextPage"
              class="rounded q-px-md slide-hover button-gm-outline-dark text-body1 font-medium button-sm"
              :class="{
                [scope.isLastPage ? 'text-primary-500' : 'text-primary']: true,
                ['hover-text-secondary']: !scope.isLastPage,
              }"
            >
              Next
              <q-icon
                size="xs"
                name="icon-arrow-narrow-right"
                class="q-pl-xs"
              />
            </q-btn>
          </div>
        </div>
      </template>

      <!-- No Data -->
      <template v-slot:no-data="{ icon, message }">
        <div class="full-width text-center">
          <div class="full-width row flex-center text-primary-500">
            <q-icon
              v-if="tableLoading || searchQuery"
              size="sm"
              class="q-pr-sm"
              :color="tableLoading ? 'secondary' : 'negative'"
              :name="
                tableLoading
                  ? 'icon-download-cloud-01'
                  : searchQuery
                  ? 'icon-filter-funnel-01'
                  : icon
              "
            />
            <span
              v-if="tableLoading"
              class="text-body2 text-primary-500 font-medium"
            >
              {{ searchQuery && !tableLoading ? 'No tasks found.' : message }}
            </span>
            <span v-else class="text-body2 text-primary-500 font-medium">
              {{
                searchQuery && !tableLoading
                  ? 'No tasks found.'
                  : `You have no ${
                      tableTab === 'tasks' ? 'tasks' : 'projects'
                    } yet.`
              }}
            </span>
          </div>
          <div
            v-if="!tableLoading && !searchQuery"
            class="full-width row flex-center text-primary-500 q-pt-sm"
          >
            <q-btn
              no-caps
              unelevated
              class="button-sm"
              color="primary"
              @click="
                $root.toggleRequest(tableTab === 'tasks' ? 'task' : 'project')
              "
            >
              <q-icon name="icon-plus" size="20px" />
              <span class="q-pl-xs"
                >Add {{ tableTab === 'tasks' ? 'Task' : 'Project' }}</span
              >
            </q-btn>
          </div>
        </div>
      </template>
    </q-table>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import { date, debounce, format } from 'quasar';
import { mapActions, mapState } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import useUser from 'stores/user';
import CardIconBox from 'src/components/Helpers/CardIconBox.vue';

export default defineComponent({
  name: 'TaskProjectList',

  components: {
    CardIconBox,
  },

  data() {
    return {
      tableTab: 'tasks',
      capitalize: format.capitalize,
      pagination: {
        descending: true,
        page: 1,
        rowsPerPage: 10,
        rowsNumber: 0,
        sortBy: 'updated_at',
        descending: true,
      },
      tableLoading: false,
      listTableDense: true,
      selectedFiles: [],
      visibleColumns: [],
      orgTmpItems: [],
      onRequestDelayed: null,
      searchQuery: '',
      orgSelectedItems: [],
    };
  },

  props: {
    brand_id: {
      type: Number,
      default: 0,
    },
  },

  async mounted() {
    this.visibleColumns = this.columns
      .map((c) => c.name) // Get all the column names
      .filter((e) => !['id', 'task_priority'].includes(e)); // Hide some columns initially
    this.onRequestDelayed = debounce(this.onRequest, 1000);
    this.onRequest({
      pagination: this.pagination,
      filter: this.searchQuery,
    });
  },

  computed: {
    ...mapState(useUser, ['user', 'selectedOrgBrands']),
    ...mapState(useGrowmodo, [
      'taskDirectory',
      'taskDirectory_Categories',
      'projectDirectory',
      'projectDirectory_Categories',
    ]),

    isTask() {
      return this.tableTab === 'tasks';
    },

    columns() {
      return this.isTask
        ? [
            {
              name: 'title',
              required: true,
              label: 'Task Type',
              align: 'left',
              field: (row) => row.title,
              format: (val) => val,
              sortable: true,
            },
            {
              name: 'id',
              label: 'Id',
              align: 'left',
              field: (row) => row.id,
              format: (val) => val,
              sortable: true,
              // sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
            },
            {
              name: 'brand_id',
              label: 'Brand',
              align: 'left',
              field: (row) => row.brand_id,
              format: (val) => this.selectedOrgBrands?.[val]?.brand_name,
              sortable: false,
              sort: false,
            },
            {
              name: 'task_priority',
              label: 'Task Priority',
              align: 'left',
              field: (row) => row.id,
              format: (val) => this.getItemPriority(val),
              sortable: false,
            },
            {
              name: 'task_type_id',
              label: 'Category',
              align: 'left',
              field: (row) => row.task_type_id,
              format: (val) =>
                Object.values(this.taskDirectory_Categories)?.filter(
                  (e) => e.id === val
                )?.[0]?.title,
              sortable: true,
              // sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
            },
            {
              name: 'created_at',
              label: 'Created On',
              align: 'left',
              field: (row) => row.created_at || 1,
              format: (val) => `${date.formatDate(val, 'MMM D, YYYY')}`,
              sortable: true,
              // sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
            },
            {
              name: 'requested_by_id',
              label: 'Created By',
              align: 'left',
              field: (row) =>
                row.requestor?.id === this.user.id
                  ? 'me'
                  : `${row.requestor?.firstname} ${row.requestor?.lastname}`,
              format: (val) => val,
              sortable: true,
              // sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
            },
          ]
        : [
            {
              name: 'title',
              required: true,
              label: 'Project Name',
              align: 'left',
              field: (row) => row.title,
              format: (val) => val,
              sortable: true,
            },
            {
              name: 'id',
              label: 'Id',
              align: 'left',
              field: (row) => row.id,
              format: (val) => val,
              sortable: true,
              sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
            },
            {
              name: 'created_at',
              label: 'Created On',
              align: 'left',
              field: (row) => row.created_at || 1,
              format: (val) => `${date.formatDate(val, 'MMM D, YYYY')}`,
              sortable: true,
              sort: (a, b) => parseInt(a, 10) - parseInt(b, 10),
            },
          ];
    },
  },

  watch: {
    tableTab: {
      async handler(val) {
        this.tableLoading = true;
        this.onRequest({
          pagination: this.pagination,
          filter: this.searchQuery,
        });
      },
    },
    searchQuery: {
      async handler(val) {
        this.tableLoading = true;
        this.onRequest({
          pagination: this.pagination,
          filter: this.searchQuery,
        });
      },
    },
    ['pagination.page']: {
      async handler(val) {
        this.tableLoading = true;
        this.onRequest({
          pagination: this.pagination,
          filter: this.searchQuery,
        });
      },
    },
  },

  methods: {
    ...mapActions(useGrowmodo, ['getTasks', 'getProjects']),

    getItemPriority(itemId) {
      const questionTypeInfo =
        (this.isTask
          ? this.getTaskInfo(itemId)
          : this.getProjectInfo(itemId)) || {};

      const itemInfo = this.getItemById(itemId);
      const dynamicQuestions = questionTypeInfo?.dynamic_questions || [];
      const dynamicAnswers = itemInfo?.dynamic_questions || [];

      const q_info = dynamicQuestions.filter((_) => _.title == 'Priority')?.[0];
      const ans_info = dynamicAnswers.filter((_) => _.id == q_info?.id)?.[0];

      const answer = ans_info?.['answer']?.[0];
      return answer?.alternative_answer || answer?.value;
    },

    getTaskInfo(taskId) {
      const taskInfo = this.getItemById(taskId);
      return this.taskDirectory[taskInfo.task_type_id];
    },

    getProjectInfo(projectId) {
      const projectInfo = this.getItemById(projectId);
      return this.projectDirectory[projectInfo.project_type_id];
    },

    getItemIndexById(id) {
      return this.orgSelectedItems.findIndex((e) => e.id === id);
    },

    getItemById(id) {
      return this.orgSelectedItems[this.getItemIndexById(id)];
    },

    async onRequest(props) {
      this.tableLoading = true;
      const { page, rowsPerPage, sortBy, descending } = props.pagination;
      const filter = props.filter;

      // fetch data from "server"
      const returnedData = await this.fetchFromServer({
        brand_id: this.brand_id || undefined,
        search: filter || undefined,
        per_page: rowsPerPage || -1,
        sort_by: sortBy || undefined,
        page: page || undefined,
        sort: descending ? 'DESC' : 'ASC',
      });

      // clear out existing data and add new
      this.orgTmpItems.splice(0, this.orgTmpItems.length, ...returnedData);

      // don't forget to update local pagination object
      if (this.pagination.page !== page) this.pagination.page = page;
      if (this.pagination.rowsPerPage !== rowsPerPage)
        this.pagination.rowsPerPage = rowsPerPage;
      if (this.pagination.sortBy !== sortBy) this.pagination.sortBy = sortBy;
      if (this.pagination.descending !== descending)
        this.pagination.descending = descending;

      // ...and turn of loading indicator
      this.tableLoading = false;
    },

    async fetchFromServer(query_props = {}) {
      const response = await this[this.isTask ? 'getTasks' : 'getProjects'](
        query_props
      );

      this.pagination.rowsNumber = response?.data?.total || 1;
      this.pagination.pagesNumber = response?.data?.last_page || 1;
      this.orgSelectedItems = response.data?.data || [];
      return this.orgSelectedItems;
    },
  },
});
</script>
