import useGrowmodo from 'stores/growmodo';
import useHelpers from 'stores/helpers';
import useUser from 'stores/user';

let user;
let growmodo;
let helpers;

function useStore() {
  user = useUser();
  growmodo = useGrowmodo();
  helpers = useHelpers();
}

async function init() {
  useStore();
  // useGrowmodo
  growmodo.getExternalLinks();
  growmodo.getUserRoles();
  growmodo.getInvitedUserRoles();
  growmodo.getBrandCategories();
  growmodo.getCompanyTypes();
  growmodo.getSubscription_Talents();
  growmodo.getSubscription_BillingTypes();
  // useHelpers
  helpers.getSocialMediaPlatforms();
}

async function init_Signup() {
  this.init();
  helpers.getEmployeeCount();
}

async function init_LoggedIn() {
  this.init();
  growmodo.getPlatforms();
  user.getOrgUsers();
  growmodo.getTaskCategories();
  growmodo.getTaskDirectory();
  growmodo.getProjectCategories();
  growmodo.getProjectDirectory();
}

async function init_RequestForm() {
  this.init();
  growmodo.getPlatforms();
  user.getOrgUsers();
  growmodo.getTaskCategories();
  growmodo.getProjectCategories();
}

export { init, init_Signup, init_LoggedIn, init_RequestForm };
