<template>
  <q-card
    flat
    bordered
    class="row items-center border-add rounded full-width cursor-pointer justify-between q-px-md q-py-sm"
    :class="`${hoverEffect ? 'hover-border-secondary' : ''}`"
    :style="`background-color: ${colorModel || 'transparent'}; ${
      small ? 'min-height: 70px' : 'min-height: 105px'
    }`"
    @contextmenu.prevent="openColorPopup"
    @dblclick="
      editableLabel
        ? (editingLabel = !editingLabel)
        : colorModel
        ? copyToClipboard(colorModel, 'Color copied to clipboard.')
        : openColorPopup()
    "
  >
    <span
      class="font-medium flex inline"
      :class="`${isColorDark(colorModel) ? 'text-white' : 'text-primary'} ${
        small ? '' : 'self-end'
      }`"
      style="max-width: 80%"
    >
      <q-input
        v-if="
          !label || label === 'Color Label' || (editableLabel && editingLabel)
        "
        dense
        autofocus
        v-model="newLabel"
        placeholder="Color Label"
        :color="isColorDark(colorModel) ? 'white' : 'primary'"
        :input-class="`${
          isColorDark(colorModel) ? 'text-white' : 'text-primary'
        }`"
        mask="NNNNNNNNNNNNNNN"
        @keydown.enter="saveLabel"
        @blur="saveLabel"
      >
        <template v-slot:append>
          <q-badge
            v-if="colorModel"
            class="text-body2"
            :class="
              isColorDark(colorModel)
                ? 'bg-translucent-light text-white'
                : 'bg-translucent-dark text-primary'
            "
          >
            {{ colorModel }}
          </q-badge>
        </template>
      </q-input>
      <template v-else> {{ newLabel }} {{ colorModel }} </template>
    </span>

    <div
      v-if="allowDelete || colorPicker"
      :class="{ ['absolute-top-right q-pa-sm q-ma-sm']: !small }"
      class="q-gutter-x-sm"
    >
      <q-btn
        v-if="allowDelete"
        flat
        size="sm"
        color="primary"
        icon="icon-trash-01"
        class="rounded ratio-1 q-pa-sm"
        :class="`${
          colorModel
            ? isColorDark(colorModel)
              ? 'bg-translucent-light text-white'
              : 'bg-translucent-dark text-primary'
            : 'bg-primary-200'
        }`"
        @click="$emit('delete')"
      />

      <q-btn
        v-if="colorPicker"
        flat
        size="sm"
        color="primary"
        icon="icon-dropper"
        class="rounded ratio-1 q-pa-sm"
        :class="`${
          colorModel
            ? isColorDark(colorModel)
              ? 'bg-translucent-light text-white'
              : 'bg-translucent-dark text-primary'
            : 'bg-primary-200'
        }`"
        @click="openColorPopup"
      />
    </div>

    <slot />

    <q-dialog
      persistent
      v-model="colorPopup"
      transition-show="scale"
      transition-hide="scale"
    >
      <ColorPicker
        style="max-width: 300px"
        :initColor="colorModel"
        :colorPalette="colorPaletteSrc"
        @saved="(val) => (colorModel = val?.color)"
        @closed="colorPopup = false"
      />
    </q-dialog>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import { Helpers_Mixin } from 'src/mixins';
import ColorPicker from 'src/components/Helpers/ColorPicker.vue';

export default defineComponent({
  name: 'ColorPickerCard',

  emits: ['update', 'updateLabel', 'delete'],

  mixins: [Helpers_Mixin],

  components: { ColorPicker },

  props: {
    allowEdit: {
      type: Boolean,
      default: true,
    },
    small: {
      type: Boolean,
      default: true,
    },
    label: {
      type: String,
      default: 'Color Label',
    },
    colorPalette: {
      type: Array,
    },
    initColor: {
      type: String,
      default: '',
    },
    colorPicker: {
      type: Boolean,
      default: true,
    },
    hoverEffect: {
      type: Boolean,
      default: true,
    },
    editableLabel: {
      type: Boolean,
      default: false,
    },
    allowDelete: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      colorModel: '',
      colorPopup: false,
      colorPaletteSrc: undefined,
      newLabel: this.label && this.label !== 'Color Label' ? this.label : '',
      editingLabel: false,
    };
  },

  watch: {
    colorModel: {
      async handler(val) {
        this.$emit('update', {
          color: val,
          dark: this.isColorDark(val),
          light: !this.isColorDark(val),
        });
      },
    },
    initColor: {
      async handler(val) {
        this.colorModel = val;
      },
    },
    colorPalette: {
      async handler(val) {
        this.colorPaletteSrc = val;
      },
      deep: true,
    },
  },

  mounted() {
    this.colorPaletteSrc = this.colorPalette;
    this.colorModel = this.initColor;
  },

  methods: {
    saveLabel() {
      if (this.newLabel) {
        this.newLabel = this.newLabel.replaceAll(' ', '_');
        const allowed = /[a-zA-Z0-9_]/;
        let formatedLabel = '';
        for (const l of this.newLabel) {
          formatedLabel += allowed.test(l) ? l : '';
        }
        if (formatedLabel) {
          this.newLabel = formatedLabel;
          this.$emit('updateLabel', this.newLabel);
          this.editingLabel = false;
        }
      }
    },

    openColorPopup() {
      if (this.allowEdit) this.colorPopup = true;
    },
  },
});
</script>
