<template>
  <q-card flat>
    <q-infinite-scroll
      @load="loadInfiniteScroll"
      :offset="0"
      class="row"
      :class="`q-col-gutter-${gridGutter}`"
    >
      <div v-if="canAdd" :class="gridClass">
        <q-card
          flat
          bordered
          class="card-quicklinks card-quicklinks-new border-add border-dashed bg-primary-1 full-height row items-center justify-center hover-border-secondary"
          @click="addBrand = true"
        >
          <q-card-section class="text-center flex-center flex">
            <q-avatar
              rounded
              size="55px"
              class="rounded q-pa-sm text-white bg-primary auto-hw"
            >
              <q-icon
                name="icon-plus"
                color="white"
                size="sm"
                class="q-pa-xs"
                style="color: white !important"
              />
            </q-avatar>
            <div class="text-body2 font-medium q-pt-md full-width">
              Add New Brand
            </div>
          </q-card-section>
        </q-card>
      </div>

      <!-- Brands List -->
      <transition
        appear
        :duration="500"
        enter-active-class="animated fadeIn"
        leave-active-class="animated fadeOut"
        v-for="(brand, i) in brands"
        :key="i"
      >
        <div :class="gridClass">
          <q-card
            flat
            bordered
            v-ripple:accent
            class="card-quicklinks brand full-height slide-hover row items-center hover-border-secondary"
            @click="$router.push('/brands/' + brand.id)"
          >
            <q-card-section
              class="self-start full-width q-px-lg q-pt-lg q-pb-md"
            >
              <div
                v-if="brandCategories?.[brand?.brand_type]?.label"
                class="full-width"
              >
                <div class="row item-center q-gutter-sm justify-between">
                  <q-badge
                    rounded
                    class="badge badge-md"
                    :class="{
                      'badge-bg-disable':
                        !brandCategories?.[brand?.brand_type]?.bg_color ||
                        brand?.deleted_at,
                      'badge-text-disable':
                        !brandCategories?.[brand?.brand_type]?.color ||
                        brand?.deleted_at,
                    }"
                    :style="`background-color: ${
                      brandCategories?.[brand?.brand_type]?.bg_color
                    }; color: ${brandCategories?.[brand?.brand_type]?.color}`"
                  >
                    {{
                      brand?.deleted_at
                        ? 'Archived'
                        : brandCategories?.[brand?.brand_type]?.label
                    }}
                  </q-badge>
                </div>
                <q-icon
                  flat
                  round
                  size="xs"
                  name="icon-dots-horizontal"
                  class="absolute-top-right all-pointer-events text-primary-500"
                  style="top: 8px; right: 8px"
                  @click.stop
                  @click.prevent
                >
                  <q-menu auto-close>
                    <q-list separator>
                      <q-item
                        clickable
                        v-if="!brand?.deleted_at"
                        class="hover-text-secondary"
                        @click.prevent="
                          addAction('archiveOrgBrand_local', brand.id)
                        "
                      >
                        <q-item-section side class="q-pr-sm">
                          <q-icon name="icon-archive" size="xs" />
                        </q-item-section>
                        <q-item-section>Archive</q-item-section>
                      </q-item>
                      <q-item
                        clickable
                        v-if="brand?.deleted_at"
                        class="hover-text-secondary"
                        @click.prevent="restoreArchiveOrgBrand_local(brand.id)"
                      >
                        <q-item-section side class="q-pr-sm">
                          <q-icon name="icon-corner-down-left" size="xs" />
                        </q-item-section>
                        <q-item-section>Restore</q-item-section>
                      </q-item>
                      <q-item
                        clickable
                        class="text-negative hover-text-negative"
                        @click.prevent="
                          addAction('deleteOrgBrand_local', brand.id)
                        "
                      >
                        <q-item-section side class="q-pr-sm">
                          <q-icon
                            name="icon-trash-01"
                            size="xs"
                            color="negative"
                          />
                        </q-item-section>
                        <q-item-section>Delete</q-item-section>
                      </q-item>
                    </q-list>
                  </q-menu>
                </q-icon>
              </div>
              <div class="text-h5 font-medium q-pt-md q-pb-sm brand-title">
                {{ limitBrandName(brand?.brand_name) }}
              </div>
            </q-card-section>
            <q-card-actions
              align="right"
              class="full-width q-px-md bg-primary-1 self-end text-secondary text-body1 font-medium"
              :to="brand.id"
              :href="brand.id"
              style="height: 56px"
            >
              View Brand
              <q-icon
                size="20px"
                name="icon-arrow-narrow-right"
                class="q-pl-sm slide-hover-left-md"
              />
            </q-card-actions>
          </q-card>
        </div>
      </transition>
    </q-infinite-scroll>

    <!-- Dynamic Dialog Confirmation -->
    <q-dialog v-model="actionConfirmation">
      <dynamic-dialog-confirmation
        v-bind="actionOptions"
        @okay="performAction"
      />
    </q-dialog>

    <!-- Add New Brand -->
    <q-dialog
      persistent
      v-model="addBrand"
      transition-show="scale"
      transition-hide="scale"
      :transition-duration="300"
    >
      <CreateNewBrand
        style="max-width: 90vw; width: 640px"
        @finished="
          () => {
            queryBrands();
            addBrand = false;
          }
        "
        @close="addBrand = false"
      />
    </q-dialog>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import { debounce } from 'quasar';
import CreateNewBrand from 'src/components/Brands/CreateNewBrand.vue';
import { mapState, mapActions } from 'pinia';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'BrandsList',

  components: { CreateNewBrand },

  props: {
    canAdd: {
      type: Boolean,
      default: true,
    },
    maxBrand: {
      type: Number,
      default: 0,
    },
    reverseList: {
      type: Boolean,
      default: true,
    },
    gridClass: {
      type: String,
      default: 'col-12 col-sm-6 col-md-3',
    },
    gridGutter: {
      type: String,
      default: 'md',
    },
    search: {
      type: String,
      default: '',
    },
    status: {
      type: String,
      default: '',
    },
    per_page: {
      type: Number,
      default: 10,
    },
    sort_by: {
      type: String,
      default: 'updated_at',
    },
    start_page: {
      type: Number,
      default: 1,
    },
  },

  data() {
    return {
      maxTitle: 33,
      addBrand: false,
      brandsResult: {},
      current_page: 1,
      last_page: 1,
      queryBrands: () => void 0,
      loadInfiniteScroll: (i, d) => (typeof d === 'function' ? d() : void 0),
      actionConfirmation: false,
      actionTargetBrand: null,
      actionName: '',
      actionOptions: {},
    };
  },

  computed: {
    ...mapState(useUser, ['selectedOrg', 'selectedOrgBrands']),
    ...mapState(useGrowmodo, ['brandCategories']),

    brands() {
      let visibleBrands = Object.values(this.brandsResult);
      // Slice to Maximimun Brands
      if (this.maxBrand && typeof this.maxBrand === 'number')
        visibleBrands = visibleBrands.slice(0, this.maxBrand);
      return visibleBrands;
    },

    totalVisibleBrands() {
      let visibleBrands = Object.values(this.brandsResult);
      // Slice to Maximimun Brands
      if (this.maxBrand && typeof this.maxBrand === 'number')
        visibleBrands = visibleBrands.slice(0, this.maxBrand);
      return visibleBrands.length;
    },

    confirmationDialogOptions() {
      return {
        archiveOrgBrand_local: {
          icon: 'icon-archive',
          okText: 'Archive',
          messageTitle: `Archive "${
            this.brandsResult[this.actionTargetBrand]?.brand_name
          }" brand?`,
        },
        restoreArchiveOrgBrand_local: {
          icon: 'icon-corner-down-left',
          okText: 'Restore',
          messageTitle: `Restore "${
            this.brandsResult[this.actionTargetBrand]?.brand_name
          }" brand?`,
        },
        deleteOrgBrand_local: {
          icon: 'icon-trash-01',
          okText: 'Delete',
          okColor: 'negative',
          messageTitle: `Confirm to delete "${
            this.brandsResult[this.actionTargetBrand]?.brand_name
          }" brand?`,
        },
      };
    },
  },

  async mounted() {
    this.current_page = this.start_page;
    this.brandsResult = this.selectedOrgBrands;
    this.queryBrands = debounce(
      this.queryBrands_Fn,
      300 /* execute at most once every 0.3s */
    );
    this.loadInfiniteScroll = debounce(
      this.loadInfiniteScroll_Fn,
      500 /* ms to wait */
    );
    this.queryBrands(this.current_page, this.per_page, true);
  },

  watch: {
    status: {
      async handler(val) {
        this.current_page = 1;
        this.queryBrands(this.current_page, this.per_page, true);
      },
    },
    search: {
      async handler(val) {
        this.current_page = 1;
        this.queryBrands(this.current_page, this.per_page, true);
      },
    },
  },

  methods: {
    ...mapActions(useUser, [
      'getOrgBrands',
      'deleteOrgBrand',
      'archiveOrgBrand',
      'restoreArchiveOrgBrand',
      'removeFromOrgBrandState',
    ]),

    addAction(method_name, brand_id) {
      if (
        typeof this[method_name] === 'function' &&
        this.brandsResult[brand_id] &&
        this.confirmationDialogOptions[method_name]
      ) {
        this.actionConfirmation = true;
        this.actionName = method_name;
        this.actionTargetBrand = brand_id;
        this.actionOptions = this.confirmationDialogOptions[method_name];
      }
    },

    performAction() {
      if (typeof this[this.actionName] === 'function')
        this[this.actionName](this.actionTargetBrand);
    },

    showActionNotif(message, success = true) {
      const params = {
        icon: success ? 'icon-check-circle-broken' : 'icon-alert-triangle',
        message,
      };
      if (!success) params.iconColor = 'negative';
      this.$q.notify(params);
    },

    archiveOrgBrand_local(brand_id, org_id = this.selectedOrg?.id) {
      this.archiveOrgBrand(brand_id, org_id).then((res) => {
        if (res.success) {
          if (!this.status || this.status == 'archived') {
            if (this.brandsResult[brand_id])
              this.brandsResult[brand_id].deleted_at = Date.now();
            this.showActionNotif('Brand has been archived');
          } else {
            if (this.brandsResult[brand_id]) delete this.brandsResult[brand_id];
          }
        } else {
          this.showActionNotif('Action failed', false);
        }
      });
    },

    restoreArchiveOrgBrand_local(brand_id, org_id = this.selectedOrg?.id) {
      const brand_info = this.brandsResult[brand_id];
      this.restoreArchiveOrgBrand(brand_info, org_id).then((res) => {
        if (res.success) {
          if (this.brandsResult[brand_id]) {
            this.brandsResult[brand_id].deleted_at = null;
            if (this.status == 'archived') delete this.brandsResult[brand_id];
            this.showActionNotif('Brand has been restored');
          }
        } else {
          this.showActionNotif('Action failed', false);
        }
      });
    },

    deleteOrgBrand_local(brand_id, org_id = this.selectedOrg?.id) {
      this.deleteOrgBrand(brand_id, org_id).then((res) => {
        if (res.success) {
          if (this.brandsResult[brand_id]) delete this.brandsResult[brand_id];
          this.showActionNotif('Brand has been removed');
        } else {
          this.showActionNotif('Action failed', false);
        }
      });
    },

    limitBrandName(brandName) {
      let addDot = brandName?.length > this.maxTitle;
      return String(brandName).slice(0, 33) + (addDot ? '...' : '');
    },

    async queryBrands_Fn(page, per_page, clearOldBrands = false) {
      if (!page) page = this.current_page;
      if (!per_page) per_page = this.per_page;
      const brandQuery = await this.getOrgBrands(false, this.selectedOrg?.id, {
        page,
        per_page,
        search: this.search,
        status: this.status,
        sort_by: this.sort_by,
        sort: this.reverseList ? 'DESC' : 'ASC',
      });

      if (brandQuery.success) {
        this.last_page = brandQuery.data?.last_page;
        const brands = brandQuery.data?.data || [];
        if (clearOldBrands) this.brandsResult = {};
        for (const brand of brands) {
          this.brandsResult[brand.id] = brand;
        }
      }
      return brandQuery;
    },

    loadInfiniteScroll_Fn(index, done) {
      if (this.current_page >= this.last_page) {
        done();
      } else {
        if (this.last_page > this.current_page) this.current_page += 1;
        this.queryBrands()?.finally?.(() => {
          done();
        });
      }
    },
  },
});
</script>
