<template>
  <q-input
    :dense="dense"
    outlined
    :autofocus="autofocus"
    hide-bottom-space
    class="full-width rounded text-body2 input-white"
    :rules="[(val) => (required ? !!val || '' : true)]"
  >
    <template v-slot:prepend>
      <slot name="prepend" />
    </template>
    <template v-slot:append>
      <slot name="append" />
    </template>
  </q-input>
</template>

<script>
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'InputTextField',

  props: {
    autofocus: {
      type: Boolean,
      default: false,
    },
    required: {
      type: Boolean,
      default: false,
    },
    dense: {
      type: Boolean,
      default: true,
    },
  },
});
</script>
