<template>
  <q-uploader
    flat
    :multiple="multiple"
    auto-upload
    ref="modelRef"
    max-file-size="5000000"
    class="full-width files-uploader rounded full-height text-primary"
    @start="startEvent"
    @finish="finishEvent"
    @added="addedEvent"
    @removed="removedEvent"
    @uploaded="uploadedEvent"
    @rejected="onRejected"
    @factoryFailed="factoryFailedEvent"
    @failed="failedEvent"
    :class="{ ['with-items']: withItems }"
  >
    <template v-slot:header="scope">
      <div
        class="q-py-lg q-px-md text-body1 text-primary-600 text-center rounded cursor-pointer full-height flex items-center rounded border-add border-dashed hover-border-secondary"
        @click="$refs?.['uploadBtnTrigger']?.click?.()"
      >
        <div class="full-width">
          <div class="full-width">
            <q-btn
              flat
              :ripple="false"
              color="primary"
              class="bg-none"
              ref="uploadBtnTrigger"
              @click="scope.pickFiles"
            >
              <CardIconBox icon="icon-upload-01" />
              <q-uploader-add-trigger />
            </q-btn>
          </div>
          <div class="q-pt-md full-width">
            <span class="text-secondary font-medium">Click to upload</span> or
            drag and drop
          </div>
          <div class="full-width font-medium">{{ note }}</div>
        </div>
      </div>
    </template>

    <template v-slot:list="scope">
      <!-- <div class="q-px-md">{{ scope?.files.map((e) => ' ' + e.name) }}</div> -->
      <div
        class="bg-white sticky absolute-top"
        style="height: 25px; z-index: 999"
        v-if="(initSlots || scope)?.files?.length"
      ></div>
      <q-list
        class="row q-col-gutter-md"
        v-if="(initSlots || scope)?.files?.length"
      >
        <template
          v-for="(file, i) in (initSlots || scope).files"
          :key="file.__key"
        >
          <div
            :class="`${
              $q.screen.sm ||
              $q.screen.lt.sm ||
              miniItems ||
              (initSlots || scope).files?.length === 1
                ? 'col-12'
                : 'col-12 col-sm-6'
            }`"
          >
            <q-item
              dense
              class="border-add rounded q-py-sm hover-up-1"
              :class="{
                ['border-secondary']: file.success === true,
                ['border-negative']: file.success === false,
                ['hover-border-secondary']: typeof file.success !== 'boolean',
              }"
            >
              <q-item-section
                thumbnail
                v-if="
                  (thumbnail && file.__img) || typeof thumbnail === 'string'
                "
                class="q-pl-md self-start"
              >
                <div
                  class="overflow-hidden flex flex-center round bg-accent"
                  style="width: 30px; height: 30px"
                >
                  <img
                    v-if="typeof thumbnail === 'boolean'"
                    :ratio="1"
                    :src="
                      file.path && !temporary
                        ? `${$cdn_host}/${file.path}`
                        : file.__img.src
                    "
                    style="width: 30px; height: unset"
                  />
                  <q-icon
                    v-else
                    :name="
                      thumbnail === 'auto'
                        ? getFileTypeIcon(file.file || file.name)
                        : thumbnail
                    "
                    size="20px"
                    color="secondary"
                  />
                </div>
              </q-item-section>

              <q-item-section class="text-left">
                <q-item-label class="full-width ellipsis">
                  <div class="text-body2">{{ file.name }}</div>
                  <div class="text-primary-400 text-body2">
                    {{ file.__sizeLabel }}
                  </div>
                </q-item-label>

                <div class="row items-center q-col-gutter-sm">
                  <div class="col-10">
                    <q-linear-progress
                      size="sm"
                      :value="file.__progress"
                      :color="
                        file.success === false || file.__status === 'failed'
                          ? 'negative'
                          : file.__progress === 1
                          ? file.__status === 'uploaded'
                            ? 'secondary'
                            : 'negative'
                          : 'secondary'
                      "
                    />
                  </div>
                  <div class="col-2 text-right">
                    {{
                      file.success === false || file.__status === 'failed'
                        ? 'Failed'
                        : file.__progressLabel
                    }}
                  </div>
                </div>
              </q-item-section>

              <q-item-section class="absolute-top-right q-ma-sm">
                <q-btn
                  flat
                  dense
                  round
                  size="sm"
                  icon="icon-trash-01"
                  class="text-primary-500"
                  :class="{
                    ['hover-text-negative']: !(
                      toDelete !== null ||
                      scope.isUploading ||
                      scope.isBusy
                    ),
                  }"
                  :disable="
                    toDelete !== null || scope.isUploading || scope.isBusy
                  "
                  @click="
                    deleteAction(initSlots || scope, initSlots ? i : file)
                  "
                />
              </q-item-section>
            </q-item>
          </div>
        </template>
      </q-list>
      <div
        v-if="scope?.files?.length > 4"
        class="absolute-bottom bg-gradient no-pointer-events sticky"
        style="height: 25px"
      ></div>
    </template>

    <slot />
  </q-uploader>
</template>

<script>
import { defineComponent } from 'vue';
import CardIconBox from 'src/components/Helpers/CardIconBox.vue';
import { mapState, mapActions } from 'pinia';
import useHelpers from 'stores/helpers';
import useUser from 'stores/user';
import { FileUploader_Mixin } from 'src/mixins';

export default defineComponent({
  name: 'FileUploader',

  mixins: [
    // Uploader functions
    FileUploader_Mixin,
  ],

  components: { CardIconBox },

  props: {
    note: {
      type: String,
      default: 'SVG, PNG, JPEG or GIF (max. 5MB)',
    },
    thumbnail: {
      type: [Boolean, String],
      default: false,
    },
    miniItems: {
      type: Boolean,
      default: false,
    },
    withItems: { type: Boolean, default: false },
    autoRemoveFailed: { type: Boolean, default: false },
    temporary: { type: Boolean, default: false },
    multiple: { type: Boolean, default: true },
    removedFile: {},
    initSlots: {},
  },

  data() {
    return {
      toDelete: null,
    };
  },

  computed: {
    ...mapState(useUser, ['activeOrgID']),
  },

  watch: {
    removedFile: {
      async handler(val) {
        if (val) {
          this.$refs['modelRef']?.abort?.(val);
          this.$refs['modelRef']?.removeFile?.(val);
        }
      },
    },
  },

  methods: {
    ...mapActions(useHelpers, ['deleteFile']),

    startEvent() {
      if (process.env.DEV_MODE) console.log('Upload startEvent');
    },
    finishEvent() {
      if (process.env.DEV_MODE) console.log('Upload finishEvent');
    },
    addedEvent(files) {
      if (process.env.DEV_MODE) console.log('Upload addedEvent', files);
    },
    removedEvent(files) {
      if (process.env.DEV_MODE) console.log('Upload removedEvent', files);
    },
    factoryFailedEvent(e) {
      if (process.env.DEV_MODE) console.error(e);
    },

    failedEvent({ files, xhr }) {
      if (process.env.DEV_MODE) console.error(files, xhr);
      const response = JSON.parse(xhr.response);
      files.forEach((file) => {
        file.success = response?.success || false;
        // Remove Failed Files
        if (this.autoRemoveFailed && files.length)
          this.$refs['modelRef']?.removeFile?.(file);
      });
    },

    uploadedEvent({ files, xhr }) {
      const response = JSON.parse(xhr.response);
      if (response?.success) {
        files.forEach((file, i) => {
          file.success = response?.success || false;
          if (this.initSlots?.files) {
            const initSlotIndex = this.initSlots.files.findIndex(
              (e) => e.__key === file.__key
            );
            const fileInfo = response.data[i];
            const slot = this.initSlots.files[initSlotIndex];
            if (slot) {
              slot.id = fileInfo.id;
              slot.file = file.name;
              slot.src = file.__img?.src;
              slot.path = fileInfo.path;
            }
          }
        });
      }
    },

    async deleteAction(fileOptions, file_d) {
      const file =
        typeof file_d === 'number' ? fileOptions?.files?.[file_d] : file_d;
      let response;
      // Add to Delete File
      this.toDelete = file;
      if (!this.temporary && file?.path)
        response = await this.deleteFile(file.id);
      if (
        this.temporary ||
        file.success === false ||
        typeof file.success !== 'boolean' ||
        (response?.success && file.__status === 'uploaded')
      ) {
        if (fileOptions?.abort) fileOptions?.abort?.(file);
        // Remove from Source
        fileOptions?.removeFile?.(file) ||
          fileOptions?.removeFileById?.(file_d);
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: `Error while deleting ${file.name}.`,
        });
      }
      this.toDelete = null;
    },
  },
});
</script>
