<template>
  <q-select
    ref="search"
    outlined
    use-input
    hide-selected
    options-dense
    hide-bottom-space
    hide-dropdown-icon
    :menu-offset="[0, 0]"
    color="primary"
    bg-color="white"
    :stack-label="false"
    v-model="requestQuery"
    class="select-request"
    popup-content-class="bg-white rounded-bottom q-px-md"
    input-class="text-h6 font-medium letter-space-normal text-primary-700"
    :placeholder="placeholder"
    :options="filteredOptions"
    @filter="filter"
    transition-show="jump-up"
    transition-hide="jump-down"
  >
    <template v-slot:prepend v-if="!show_button">
      <q-icon
        name="icon-search-lg"
        size="sm"
        class="text-primary-700 q-pa-xs hover-text-secondary"
      />
    </template>

    <template v-slot:no-option v-if="false">
      <q-item>
        <q-item-section>
          <div class="text-center">
            <q-spinner-pie color="primary" size="24px" />
          </div>
        </q-item-section>
      </q-item>
    </template>

    <template v-slot:option="scope">
      <q-item
        v-if="scope.opt.istitle"
        :key="scope.itemProps.id + '_title'"
        :to="scope.opt.url"
        @click="scope.opt.action"
        class="q-pt-md rounded"
        style="min-height: unset"
      >
        <q-item-section>
          <q-item-label class="text-body2 font-medium text-primary-500">{{
            scope.opt.title
          }}</q-item-label>
        </q-item-section>
      </q-item>
      <template v-else-if="scope.opt.showmore">
        <q-item
          clickable
          :key="scope.itemProps.id + '_more'"
          @click="scope.opt.action"
          class="q-mb-md bg-none rounded"
        >
          <q-item-section class="cursor-pointer">
            <q-item-label class="text-dody2 font-medium text-secondary">{{
              scope.opt.title
            }}</q-item-label>
          </q-item-section>
        </q-item>
        <q-separator v-if="scope.index !== filteredOptions.length - 1" />
      </template>
      <q-item
        v-else
        clickable
        :to="scope.opt.url"
        v-bind="scope.itemProps"
        class="text-body2 font-medium rounded bg-none text-primary hover-bg-accent"
        :class="
          selectedOrgIsPaused || !testOnMaintenance(scope.opt)
            ? 'cursor-not-allowed'
            : 'hover-text-secondary greyscale'
        "
        :key="scope.itemProps.id"
        @click.prevent="
          () => {
            if (selectedOrgIsPaused) {
              $q.notify({
                icon: 'icon-alert-triangle',
                iconColor: 'negative',
                message: 'Your subscription is paused, please resume it first.',
              });
              return;
            }
            if (!testOnMaintenance(scope.opt)) {
              $q.notify({
                icon: 'icon-alert-triangle',
                iconColor: 'negative',
                message: 'Not available in maintenance plan.',
              });
              return;
            }
            this.$root.requestAction(
              scope.opt?.directoryType === 'tasks' ? 'task' : 'project',
              scope.opt
            );
          }
        "
      >
        <q-item-section side>
          <CardIconBox
            :icon="scope.opt?.icon || 'icon-browser'"
            :iconColor="
              !selectedOrgIsPaused && testOnMaintenance(scope.opt)
                ? 'secondary'
                : 'primary-500'
            "
          />
        </q-item-section>
        <q-item-section>
          <q-item-label
            :class="
              !selectedOrgIsPaused && testOnMaintenance(scope.opt)
                ? 'text-primary'
                : 'text-primary-500'
            "
          >
            {{ scope.opt.title }}
          </q-item-label>
        </q-item-section>
        <q-item-section side v-if="scope.opt.type">
          <q-badge
            v-if="true"
            class="badge"
            :class="`badge-${
              !selectedOrgIsPaused && testOnMaintenance(scope.opt)
                ? 'blue'
                : 'grey'
            }`"
          >
            {{ scope.opt.type }}
            <q-icon
              v-if="scope.opt.type == 'Search'"
              size="14px"
              name="icon-corner-down-right"
              class="text-primary-500 q-ml-xs"
            />
          </q-badge>
          <q-btn
            v-else
            dense
            outline
            rounded
            no-caps
            text-color="secondary"
            size="12px"
            class="bg-primary-50 q-px-sm hover-text-secondary not-gm"
          >
            {{ scope.opt.type }}
            <q-icon
              v-if="scope.opt.type == 'Search'"
              size="14px"
              name="icon-corner-down-right"
              class="text-primary-500 q-ml-xs"
            />
          </q-btn>
        </q-item-section>
      </q-item>
    </template>

    <template v-slot:after v-if="show_button">
      <q-avatar
        rounded
        size="75px"
        class="cursor-pointer rounded text-white bg-primary q-pa-sm"
        style="height: auto; width: auto; height: auto"
        @click="$root.toggleRequest()"
      >
        <q-icon :name="leftIcon" size="sm" class="q-pa-sm" color="white" />
      </q-avatar>
    </template>
  </q-select>
</template>

<script>
import { defineComponent, ref } from 'vue';
import { mapState } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import useUser from 'stores/user';
import { searchInObjectArray } from 'src/assets/scripts/functions';
import CardIconBox from 'src/components/Helpers/CardIconBox.vue';

export default defineComponent({
  name: 'SearchDropdown',

  emits: ['searching'],

  components: { CardIconBox },

  props: {
    show_button: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: 'What is your next request?',
    },
    requestOptions: {
      type: Array,
      default: () => [
        //   {
        //     title: 'Label Title',
        //     url: '/',
        //     category: 'Category',
        //     icon: 'icon-browser',
        //   },
      ],
    },
    leftIcon: {
      type: String,
      default: 'icon-plus',
    },
  },

  setup() {
    const requestQuery = ref('');
    const options = ref(null);
    const filteredOptions = ref([]);
    const search = ref(null); // $refs.search
    return {
      search,
      options,
      requestQuery,
      filteredOptions,
    };
  },

  computed: {
    ...mapState(useUser, ['selectedOrgIsMaintenance', 'selectedOrgIsPaused']),
    ...mapState(useGrowmodo, [
      'taskDirectory',
      'projectDirectory',
      'maintenanceTaskCatId',
    ]),

    tasksList() {
      return Object.values(this.taskDirectory);
    },

    projectsList() {
      return Object.values(this.projectDirectory);
    },
  },

  data() {
    return {
      requestOptions_Internal: [],
    };
  },

  methods: {
    isTask(item) {
      return item.directoryType === 'tasks';
    },

    isMaintenanceTask(task) {
      return (
        this.isTask(task) && task?.tags?.includes?.(this.maintenanceTaskCatId)
      );
    },

    testOnMaintenance(item) {
      if (this.selectedOrgIsMaintenance) {
        // Subscription is on Maintenance Plan
        if (!this.isMaintenanceTask(item)) return false;
      }
      return true;
    },

    updateSearch_Source() {
      if (!this.requestOptions || !this.requestOptions.length) {
        this.tasksList.forEach((e) => (e.directoryType = 'tasks'));
        this.projectsList.forEach((e) => (e.directoryType = 'projects'));
        let _tasks = this.insertCategory('Tasks', this.tasksList, {
          action: () => this.$root.toggleRequest('task'),
        });
        let _projects = this.insertCategory('Projects', this.projectsList, {
          action: () => this.$root.toggleRequest('project'),
        });
        this.requestOptions_Internal = [..._tasks, ..._projects];
      } else {
        this.requestOptions_Internal = this.requestOptions;
      }
      return this.requestOptions_Internal;
    },

    insertCategory(
      category,
      array_value = [],
      showmore = { title: 'Show more', action: () => {} }
    ) {
      if (!category || !array_value || !array_value.length) return [];
      return [
        // Limited to first 4
        {
          title: category,
          istitle: true,
        },
        ...array_value.slice(0, 4),
        {
          title: showmore?.title || 'Show more',
          showmore: true,
          action: showmore?.action || void 0,
        },
      ];
    },

    filter(val, update) {
      this.$emit('searching', { val, update });
      if (this.options === null) {
        // Load data
        setTimeout(() => {
          this.options = this.requestOptions_Internal;
          this.search.filter('');
        }, 100);
        update();
        return;
      }
      if (val === '') {
        update(() => {
          this.filteredOptions = this.updateSearch_Source();
        });
        return;
      }
      update(() => {
        let result = [];
        if (!this.requestOptions || !this.requestOptions.length) {
          // Tasks
          let _tasks_result = this.insertCategory(
            'Tasks',
            searchInObjectArray(this.tasksList, val),
            {
              action: () => this.$root.toggleRequest('task'),
            }
          );
          result = result.concat(_tasks_result);
          // Projects
          let _projects_result = this.insertCategory(
            'Projects',
            searchInObjectArray(this.projectsList, val),
            {
              action: () => this.$root.toggleRequest('project'),
            }
          );
          result = result.concat(_projects_result);
        } else {
          let _query = searchInObjectArray(this.updateSearch_Source(), val);
          result = result.concat(_query);
        }
        this.filteredOptions = result;
        // [
        //   {
        //     title: val,
        //     icon: 'icon-search-lg',
        //     type: 'Search',
        //   },
        // ];
      });
    },
  },

  beforeMount() {
    this.updateSearch_Source();
  },
});
</script>
